import React from 'react';

class PortfolioCard extends React.Component {
  renderGithubLink() {
    if (this.props.githubLink) {
      return (
        <a href={this.props.githubLink} target="_blank" className="m-1 btn btn-outline-danger">
          View Code
        </a>
      );
    }
  }

  siteLink() {
    if (this.props.siteLink) {
      return (
        <a href={this.props.siteLink} target="_blank" className="m-1 btn btn-outline-danger">
          Go to Site
        </a>
      );
    }
  }

  render() {
    return (
      <div className="col-sm-6 col-lg-4">
        <div className="card portfolio text-center">
          <img src={this.props.img} className="card-img-top border" alt="..." />
          <div className="card-header">
            <h5 className="card-title">{this.props.title}</h5>
          </div>
          <div className="card-body">
            <p className="card-text">{this.props.description}</p>
          </div>
          <div className="card-footer">
            {this.renderGithubLink()}
            {this.siteLink()}
          </div>
        </div>
      </div>
    );
  }
}

export default PortfolioCard;
