import React from 'react';
import { Link } from 'react-router-dom';

class NavBar extends React.Component {
  state = { navClass: 'navbar fixed-top navbar-expand-md' };

  componentDidMount() {
    window.addEventListener('scroll', this.onScroll);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.onScroll);
  }

  onScroll = (e) => {
    if (document.body.scrollTop >= 3 || document.documentElement.scrollTop >= 3) {
      if (this.state.navClass.includes('scroll')) {
        return;
      }
      this.toggleNavBackground();
    } else {
      this.toggleNavBackground(null, true);
    }
  };

  toggleNavBackground = (_e, turnOff = false) => {
    if (this.state.navClass.includes('scroll') || turnOff) {
      this.setState({ navClass: 'navbar fixed-top navbar-expand-md' });
    } else {
      this.setState({ navClass: 'navbar fixed-top navbar-expand-md scroll' });
    }
  };

  render() {
    return (
      <nav id="navbar" className={this.state.navClass}>
        <a className="navbar-brand" href="/" />

        <button
          onClick={this.toggleNavBackground}
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon">
            <i className="fas fa-bars pt-1" />
          </span>
        </button>

        <div className="collapse navbar-collapse justify-content-center" id="navbarSupportedContent">
          <div className="navbar-nav">
            <Link to="/" className="nav-item nav-link p-4">
              About me
            </Link>
            <Link to="/portfolio" className="nav-item nav-link p-4">
              Portfolio
            </Link>
            <Link to="/blog" className="nav-item nav-link p-4">
              Blog
            </Link>
          </div>
        </div>
      </nav>
    );
  }
}

export default NavBar;
